@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');

body {
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
/* header style */
.header-logo{
  height: 100px;
  display: block;
  margin: 0 auto;
}
.main-header nav{
  display: flex;
  list-style: none;
  background: black;
  padding: 15px;
 
}
.main-header nav li{
  margin-right: 20px;
  padding: 10px;
}
.main-header nav li:hover{
  background-color: lightgray;
}
.main-header nav li a{
  text-decoration: none;
  color: ghostwhite;
  font-size: 20px;
}
/* product design */
.shop-container{
 display: grid;
 grid-template-columns: 3fr 1fr;
}
.all-product{
  border-right: 1px solid dimgray;
  margin-right: 10px;
  margin-left: 10%;
}
/* single product style */
.product{
  display: grid;
  grid-template-columns: 1fr 2fr;
  border-bottom: 1px solid dimgray;
  margin: 15px;
}
.product-name{
  color: rgb(32, 163, 250);
  font-weight: 500;
}
/* button style */
.btn-buy{
  background-color: #FF9800;
  height: 40px;
  width: 125px;
  border: 1px solid lightgrey;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 700;
  margin-bottom: 10px;
}